import React, { useState, useEffect } from 'react'
import axios from 'axios'

export default function ProductList({ searchTerm, cart, setCart }) {

    const [products, setProducts] = useState()    

    useEffect( () => {
        fetchProducts()
    }, [])

    const addToCart = item => {
        const newItem = [...cart, item]
        setCart(newItem)
    }

    const fetchProducts = async () => {
        const res =  await axios.post('/products')
        setProducts(res.data.data)
    }

    const selectQuantity = item => {
        let options = []
        for (let i = 1; i <= item.quantity; i ++) {
            options.push(<option key = {i}>{i}</option>)
        }
        return options
    }

    const filterProducts = () => {
        return products
        .filter( item => item.name.toLowerCase().includes(searchTerm.toLowerCase()) || item.type.toLowerCase().includes(searchTerm.toLowerCase()) )
        .map( (item, id) => {

        return (
            <div className="productlisting" key = {id}>
            <img src = {item.picture} alt = {item.name} style = {{'height' : '10vh', 'width' : '5vw'}} />
            <span>{item.name}</span>
            <p>Unit Price: $20</p>
            <p>Units in Stock: {item.quantity}</p>
            <select>
                {selectQuantity(item)}
            </select>
            <button
            onClick = { () => addToCart(item) }
            className="addtocart">Add to Cart</button>
            </div>
        )})
    }

    return(

        <div className="productlist">

            {
                products &&
                filterProducts()
            }

        </div>
    )
}