import React, { useState } from 'react'
import Searchbar from './Searchbar'
import ProductList from './ProductList'

export default function CreateOrder({ setActiveNavItem, cart, setCart }) {

    const [searchTerm, setSearchTerm] = useState('')

    return(

        <div className="createorder">

            <h1 style = {{'fontSize' : '5vw'}}>Create Order</h1>

            <div
            onClick = {() => setActiveNavItem('cart')}
            className="viewcart">
                <p>View Cart</p>
                <img src = 'http://cdn.onlinewebfonts.com/svg/img_416975.png' alt = 'cart' style = {{'height': '50px'}} />
                <p style = {{'position': 'relative', 'right' : '2.5vw', 'top' : '-1vw', 'color' : '#FD992E'}}>{cart.length}</p>
            </div>

            <Searchbar setSearchTerm = {setSearchTerm} />

            <ProductList searchTerm = {searchTerm} cart = {cart} setCart = {setCart} />

        </div>
    )
}