import React from 'react'

export default function Cart({ cart }) {

    return(

        <div className="cart">
            
            {cart.map( (item, id) => {
                return (
                    <div className="productlisting" key = {id}>
                    <img src = {item.picture} alt = {item.name} style = {{'height' : '10vh', 'width' : '5vw'}} />
                    <span>{item.name}</span>
                    <p>Unit Price: $20</p>
                    <p>Units in Stock: {item.quantity}</p>
                    </div>
                )}
            )}

        </div>

    )
}